import React from "react";
import ReactDOM from "react-dom";
import {
  AdaptivityProvider,
  ConfigProvider,
  useAdaptivity,
  AppRoot,
  SplitLayout,
  SplitCol,
  ViewWidth,
  View,
  Panel,
  PanelHeader,
  Header,
  Group,
  SimpleCell,
  PromoBanner,
  FixedLayout
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

import bridge from '@vkontakte/vk-bridge';
import VK from "@vkontakte/vkui";
const App = () => {
  const { viewWidth } = useAdaptivity();

  const promoBannerProps = {
    title: 'Заголовок',
    domain: 'vk.com',
    trackingLink: 'https://vk.com',
    ctaText: 'Перейти',
    advertisingLabel: 'Реклама',
    iconLink: 'https://sun9-7.userapi.com/c846420/v846420985/1526c3/ISX7VF8NjZk.jpg',
    description: 'Описание рекламы',
    ageRestrictions: "14+",
    statistics: [
      { url: '', type: 'playbackStarted' },
      { url: '', type: 'click' }
    ]
  };
  
  
  //vk

  bridge.send("VKWebAppInit", {});

  bridge.send('VKWebAppGetAds')
      .then((promoBannerProps) => {
        this.setState({ promoBannerProps });
      })
  { promoBannerProps && <PromoBanner bannerData={ promoBannerProps } /> }
  
  //
  return (
      <AppRoot>
        <SplitLayout header={<PanelHeader separator={false} />}>
          <View activePanel="promo">
            <Panel id="promo">
              <FixedLayout vertical="bottom">
                <PromoBanner bannerData={promoBannerProps} />
              </FixedLayout>
            </Panel>
          </View>
          <SplitCol spaced={viewWidth && viewWidth > ViewWidth.MOBILE}>
            <View activePanel="main">
              <Panel id="main">
                <PanelHeader>VKUI</PanelHeader>
                <Group header={<Header mode="secondary">Items</Header>}>
                  <SimpleCell>Hello</SimpleCell>
                  <SimpleCell>World</SimpleCell>
                </Group>
              </Panel>
            </View>
          </SplitCol>
        </SplitLayout>
      </AppRoot>
  );
};

ReactDOM.render(
    <ConfigProvider>
      <AdaptivityProvider>
        <App />
      </AdaptivityProvider>
    </ConfigProvider>,
    document.getElementById("root")
);

export default App;
